.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.top-16 {
  top: 4rem;
}

.pin-bot-left {
  bottom: 0;
  left: 0;
}

.pin-bot-right {
  bottom: 0;
  right: 0;
}

.hide-proj {
  right: 1rem;
}

.topbar {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4rem;
  z-index: 100;
}

.sidebar {
  position: absolute;
  display: none;
  z-index: 100;
}

.collapsed-sidebar {
  position: absolute;
  display: none;
  z-index: 100;
}

.sidebar-toggle {
  position: absolute;
  display: none;
  z-index: 99;
}

.main {
  height: 100vh;
  width: 100vw;
}

.cm-s-code-editor {
  flex: 1;
  height: 100% !important;
  width: 67vw !important;
}

.code-container {
  position: absolute;
  top: initial;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  height: 14rem;
  z-index: 100;
}

.collapsed-code {
  position: absolute;
  display: flex;
  top: initial;
  bottom: 0;
  left: 0;
  right: 0;
  height: 4rem;
  width: 100vw;
  z-index: 100;
}

.code-toggle {
  position: absolute;
  display: flex;
  bottom: 0;
  left: 1rem;
  width: 2rem;
  z-index: 99;
}

.run-code {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  width: 5rem;
  height: 2rem;
  z-index: 5;
}

.clear-code {
  position: absolute;
  bottom: 1rem;
  right: 7rem;
  width: 5rem;
  height: 2rem;
  z-index: 5;
}
  
.eval-code {
  position: absolute;
  bottom: 1rem;
  right: calc(33vw + 1rem);
  width: 6rem;
  height: 2rem;
  z-index: 5;
}
  
.beautify-code {
  position: absolute;
  bottom: 1rem;
  right: calc(33vw + 8rem);
  width: 6rem;
  height: 2rem;
  z-index: 5;
}

/* sm */
@media (min-width: 640px) {

}

/* md */
@media (min-width: 768px) {

}

/* lg */
@media (min-width: 1024px) {
  .topbar {
    display: none;
  }
  
  .sidebar {
    display: flex;
    top: 0;
    bottom: 0;
    left: 0;
    width: 16rem;
  }
  
  .collapsed-sidebar {
    display: flex;
    top: 0;
    bottom: 0;
    left: 0;
    width: 4rem;
  }

  .collapsed-sidebar .sidebar-items {
    width: 4rem;
  }

  .sidebar-toggle {
    display: flex;
    height: 2rem;
    bottom: 1rem;
  }

  .cm-s-code-editor {
    flex: 1;
    height: 75vh !important;
    width: auto !important;
  }

  .code-container {
    display: flex;
    top: 0;
    bottom: 0;
    left: initial;
    right: 0;
    width: 16rem;
    height: 100vh;
  }

  .collapsed-code {
    top: 0;
    bottom: 0;
    left: initial;
    right: 0;
    height: 100vh;
    width: 4rem;
  }

  .code-toggle {
    bottom: 1rem;
    left: initial;
    right: 0;
    height: 2rem;
  }
  
  .eval-code {
    bottom: calc(25vh + 1rem);
    right: 1rem;
  }
  
  .beautify-code {
    bottom: calc(25vh + 1rem);
    right: 8rem;
  }
}

/* xl */
@media (min-width: 1280px) {
  .topbar {
    display: none;
  }
  
  .eval-code {
    bottom: calc(25vh + 1rem);
    right: 1rem;
  }
  
  .beautify-code {
    bottom: calc(25vh + 1rem);
    right: 8rem;
  }
}

.absolute-full {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}